@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@300&family=Noto+Sans+JP:wght@300&family=Roboto:wght@900&display=swap);
html{background-color:#0a192e}body{margin:0;font-family:"Inconsolata","Roboto","Noto Sans JP","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}
.App{background-color:#0a192e;color:#fff}main{margin:0px auto;width:100%;max-width:1600px;min-height:100vh}section{flex-direction:column;align-items:flex-start;min-height:100vh;max-width:900px;margin:0px auto;padding:100px 0px}@media(max-width: 480px)and (min-height: 700px){section{padding-bottom:10vh}}.numbered-heading{display:flex;align-items:center;position:relative;margin:10px 0px 40px;width:100%;font-family:"Roboto";font-size:clamp(26px,5vw,37px);white-space:nowrap;color:#ccd6f6}.numbered-heading:after{content:"";display:block;position:relative;width:300px;height:1px;margin-left:20px;background-color:#273957}
.menuNav{color:#f0f8ff;list-style-type:decimal-leading-zero;background-color:#0a192e;font-family:"Inconsolata"}@media(max-width: 480px)and (min-height: 700px){.menuNav{padding-bottom:10vh}}
.sns{width:40px;position:fixed;bottom:0;left:40px;right:auto;z-index:10}@media(max-width: 1080px){.sns{right:auto 20px}}@media(max-width: 768px){.sns{display:none}}.sns ul{display:flex;flex-direction:column;align-items:center;margin:0;padding:0;list-style:none}.sns ul:after{content:"";display:block;width:1px;height:90px;margin:0 auto;background-color:#a8b3d0}.sns ul li{padding:10px}.sns ul li:last-of-type{margin-bottom:20px}.sns ul li a{color:#a8b3d0;padding:10px}.sns ul li a:hover,.sns ul li a:focus{transform:translateY(-3px);color:#7511f6}.sns ul li a svg{width:20px;height:20px}
#hero{font-family:"Roboto";display:flex}#hero h2{margin:0px 0px 30px 4px;color:#7511f6;font-size:clamp(10px,8vw,20px);font-weight:400}#hero .big-heading{margin:0px;font-size:clamp(40px,8vw,100px);color:#ccd6f6}#hero .hand{margin:auto;font-size:clamp(40px,8vw,100px);color:#7511f6;transform-origin:left bottom;animation:1s infinite rotation}@keyframes rotation{0%{transform:rotate(0)}50%{transform:rotate(10deg)}100%{transform:rotate(0deg)}}
#about{font-family:"Noto Sans JP"}#about .contents-body{color:#8893af;font-family:"Roboto";font-weight:bold}#about ul.skills-list{display:grid;grid-template-columns:repeat(3, minmax(140px, 200px));grid-gap:0 10px;padding:0;margin:20px 0 0 0;overflow:hidden;list-style:none}@media(max-width: 580px){#about ul.skills-list{grid-template-columns:repeat(2, minmax(140px, 200px))}}#about ul.skills-list li{position:relative;margin-bottom:10px;padding-left:20px;font-family:"Inconsolata";font-size:16px}#about ul.skills-list li:before{content:"▹";position:absolute;padding-top:6px;left:0;color:#7511f6;line-height:12px}
#experience .time{color:#a8b3d0}
.product-card a{color:#a8b3d0}.product-card a:hover,.product-card a:focus{color:#7511f6}.product-card a svg{width:20px;height:20px}
footer{display:flex;justify-content:center;align-items:center;flex-direction:column;height:auto;min-height:70px;padding:15px;text-align:center}footer .footer-sns{display:none;font-size:20px;line-height:1.3}@media(max-width: 768px){footer .footer-sns{display:block;width:100%;max-width:270px;margin:0 auto 10px}}footer .footer-sns ul{display:flex;justify-content:space-between;align-items:center;padding:0px;margin:0px;list-style:none}footer .footer-sns ul a{display:list-item;color:#a8b3d0;padding:10px}footer .footer-sns ul a svg{width:20px;height:20px}footer .footer-info{font-size:18px;line-height:1;font-family:"Inconsolata"}footer .footer-info a{padding:10px;text-decoration:none;color:#a8b3d0}footer .footer-info a:hover{color:#7511f6}
